export default {
	data() {
		return {
			paramsCarType : 0,
			carType:[
				{name:'全部',id:0},
				{name:'入门型',id:1},
				{name:'新能源型',id:2},
				{name:'商务型',id:3},
				{name:'豪华型',id:4},
			],
			carTypeAll:[]
		}
	},
	onLoad(params) {
		if(params && params.type){
			this.paramsCarType = params.type;
		}
		
	},
	onShow() {
		const that = this;
		that.$http.requestGet({
			url:`${that.$baseUrl}/api/car/getCarsByType`,
			data:{
				tid:this.paramsCarType
			},
			success:res=>{
				const data = res.data;
				if(data.length > 0){
					this.carTypeAll = data;
				}else {
					//暂无数据
				}
				
			} 
		})  
		
	},
	methods: { 
		chooseCarType(i) {
			const that = this;
			that.paramsCarType = i;
			that.carTypeAll = [];
			that.$http.requestGet({
				url:`${that.$baseUrl}/api/car/getCarsByType`,
				data:{
					tid:this.paramsCarType
				},
				success:res=>{
					const data = res.data;
					if(data.length > 0){
						this.carTypeAll = data;
					}else {
						//暂无数据
					}
					
				}    
			})   
		},
		carDetail(id) {
		    uni.navigateTo({
				url: `/pages/choosedCarType/carDetail?id=${id}`
			})
		}
	}
}
